<template>
	<div>
		<v-form ref="form" v-model="valid">
      <v-row dense>
        <v-col cols="12" md="4" sm="6" class="py-0">
          <v-select
            label="Kelompok Prodi"
            outlined
            dense
            v-model="dataFilter.kelompok"
            :items="kelompok"
            required
            :rules="requiredRule"
          ></v-select>
        </v-col>
        <v-col cols="12" md="4" sm="6" class="py-0">
          <v-autocomplete
            label="PTN"
            outlined
            dense
            v-model="dataFilter.ptn"
            :items="dataPtnByNama"
            item-text="nama"
            item-value="nama"
            required
            :rules="requiredRule"
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="4" sm="6" class="py-0">
          <v-btn
            type="submit"
            :disabled="!valid"
            @click.prevent="validate"
            color="primary"
            depressed>
            filter
          </v-btn>
        </v-col>
      </v-row>
    </v-form>

    <DataPTN/>
	</div>
</template>

<script>
  import { mapState } from 'vuex'
  import DataPTN from '@/components/PTN/DataPTN'

  export default{
    data: () => ({
      valid: true,
      requiredRule: [v => !!v || 'Data harus diisi'],
      kelompok: ['SAINTEK', 'SOSHUM'],
      dataFilter: {
        provinsi: '',
        kelompok: ''
      }
    }),

    components: {
      DataPTN
    },

    created() {
      this.$store.dispatch('dataTempat/getDataProv')
      this.$store.dispatch('dataPtn/getDataPtnByName')
    },

    computed: {
      ...mapState('dataTempat', ['dataProv']),
      ...mapState('dataPtn', ['dataPtnByNama'])
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.$store.dispatch('dataPtn/getDataPtnFilter', this.dataFilter)
          this.$store.dispatch('dataPtn/updateIsLoading', true)
        }
      }
    }
  }
</script>