<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			:headers="header"
			:items="dataPtn"
			:search="search"
			:items-per-page="20"
			:hide-default-footer="lengthPage===0?false:true">		
			<template v-slot:top>
				<v-text-field
	        v-model="search"
	        prepend-icon="mdi-magnify"
	        label="Cari PTN"
	        single-line
	        autocomplete="off"
	        hide-details
	      ></v-text-field>
			</template>
	    <template v-slot:item.no="{item}">
	    	{{dataPtn.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn 
					icon color="success" 
					@click="$router.push({ name: 'preview-ptn' })">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<v-btn icon color="primary">
					<v-icon small>mdi-pencil</v-icon>
				</v-btn>
				<v-btn icon color="red">
					<v-icon small>mdi-trash-can</v-icon>
				</v-btn>
			</template>		
		</v-data-table>

		<v-pagination
			class="mt-3"
			v-model="page"
			:length="lengthPage"
		></v-pagination>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama PTN', value: 'nama', sortable: false },
				{ text: 'Program Studi', value: 'prodi.nama', sortable: false },
				{ text: 'Kode Prodi', value: 'prodi.kode', sortable: false },
				{ text: 'Kelompok Prodi', value: 'prodi.kelompok', sortable: false },
				{ text: 'Mapel Kompetensi', value: 'mapel', sortable: false },
				{ text: 'NRM', value: 'nrm', sortable: false },
				{ text: 'Portofolio', value: 'portofolio', sortable: false },
				{ text: 'IK', value: 'ik', sortable: false },
				{ text: 'DT', value: 'daya_tampung', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],
			page: 1,
			search: ''
		}),

		components: {
			Loader
		},

		created() {
			this.$store.dispatch('dataPtn/getDataPtn', this.page)
			this.$store.dispatch('dataPtn/updateIsLoading', true)
		},

		watch: {
			page(val) {
				this.$store.dispatch('dataPtn/getDataPtn', val)
				this.$store.dispatch('dataPtn/updateIsLoading', true)
			}
		},

		computed: {
			...mapState('dataPtn', ['isLoading', 'dataPtn', 'lengthPage'])
		}
	}
</script>